.goo1,
.goo2 {
  -webkit-filter: url(#goo);
}

.holder div {
  background: #fffff0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.goo1 {
  position: relative;
  top: 3em;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}

.goo1 > div {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform-origin: -50% -50%;
  animation: circle 2.5s infinite cubic-bezier(.57, 0, .52, 1);
}

.goo1 > div:nth-child(1) {
  animation-delay: 0.25s;
}

.goo1 > div:nth-child(2) {
  animation-delay: 0.5s;
}

.goo1 > div:nth-child(3) {
  animation-delay: 0.75s;
}

.goo1 > div:nth-child(4) {
  animation-delay: 1s;
}

.goo1 > div:nth-child(5) {
  animation-delay: 1.25s;
}

.goo2 > div {
  transform: translateX(0);
  animation: sideways 2.5s infinite ease;
}

.goo2 > div:nth-child(1) {
  animation-delay: 0.25s;
}

.goo2 > div:nth-child(2) {
  animation-delay: 0.5s;
}

.goo2 > div:nth-child(3) {
  animation-delay: 0.75s;
}

.goo2 > div:nth-child(4) {
  animation-delay: 1s;
}

.goo2 > div:nth-child(5) {
  animation-delay: 1.25s;
}

.goo2 {
  padding-left: 40px;
  width: 200px;
  height: 100px;
}
@keyframes circle {
  50% {
    width: 50px;
    height: 50px;
  }

  100% {
    transform: rotate(360deg);
  }
}
@keyframes sideways {
  33% {
    transform: translateX(50px);
  }

  66% {
    transform: translateX(-50px);
  }
}



